import React from 'react';
import { IonContent, IonHeader, IonItem, IonLabel, IonPage, IonText, IonThumbnail, IonTitle, IonToolbar } from '@ionic/react';
import ExploreContainer from '../components/ExploreContainer';
import './Tab4.css';

const Tab4: React.FC = () => {
  return (
    <IonPage>
      <IonHeader>
        <IonToolbar >
        <IonItem>
           <IonLabel>
              <IonText color="light">
              <IonTitle>Nosotros</IonTitle> 
              </IonText>
            </IonLabel>
            <IonThumbnail slot="end">
            <img alt="Logo San Antonio de Areco" src={require('../assest/image/logofull.png') } width="40" height="40" />
          </IonThumbnail>
        </IonItem> 
        </IonToolbar>
      </IonHeader>
      <IonContent fullscreen>
     
        <p>Esta aplicacin es un desarrollo del Municipio de San Antonio de Areco y forma parte 
          de su Sistema Municipal de Gestin de Riesgos (SMGR) para alertar de manera temprana 
          sobre las crecidas del Ro Areco y de los fenmenos meteorolgicos que puedan afectar a la ciudad.</p>
        <p>El SMGR y la aplicacin "SAT Areco" son acciones de Adaptacin al Cambio Climtico
           definidas en el Plan Local de Accin Climtica de San Antonio de Areco 2020-2030.</p>
          <img alt="Foto San Antonio de Areco" src={require('../assest/image/banner.png')}  width="390" height="240" className="logobanner" />
      </IonContent>
 
    </IonPage>
  );
};

export default Tab4;